* {
  box-sizing: border-box;
}

:root {
  --bgcolor: #333;
  --textcolor: white;
  --midcolor: #aaa;
  --accentcolor: #d7d;
  --accentcolorhighlight: #f7f;
  --subtlecolor: #444;
}

html, body {
  margin: 0;
  background-color: var(--bgcolor);
  font-family: "Open Sans", Arial, sans-serif;
  font-weight: 300;
}


b, strong {
  font-weight: bold;
}


.navbar {
  display: flex;
  color: var(--textcolor);
  padding: 1rem;
  width: 100vw;
  max-width: 35rem;
  position: fixed;
  top: 0;
  left: 0;
  margin-left: 50vw;
  transform: translateX(-50%);
  z-index: 10;
}

.logo {
  flex: 1;
  font-weight: 300;
}



.metronome {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: auto;
  border-radius: .5rem;
  align-items: center;
}

.inputs {
  width: 100vw;
  max-width: 35rem;
  display: flex;
  flex-direction: column;
  background-color: transparent;
  color: var(--textcolor);
  padding: 2rem 0;
  position: fixed;
  bottom: 0;
}

.input-row {
  padding: 1rem;
  font-size: 1rem;
  width: 100%;
  align-self: center;

}

.tempo-input-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.large {
  font-size: 3rem;
  color: var(--textcolor);
}

.tempo-label {
  width: 15rem;
  color: #aaa;
}




.radio-input-section {
  display: flex;
  flex-direction: column;
  padding: 1rem 0;

}

.radio-input-section .label {
  font-size: 1.5rem;
}

.radio-scrollable {
  white-space: nowrap;
  overflow-x: auto;
}



button {
  color: var(--textcolor);
  background-color: transparent;
  width: 3rem;
  height: 3rem;
  text-align: center;
  border: 1px solid var(--subtlecolor);
  border-radius: 50%;
  display: block;
  margin: 0;
  transition: .3s;
  cursor: pointer;
  font-family: 'Lato';
  font-weight: 100;
  font-size: 1rem;
  flex-shrink: 0;
}
button:hover {
  
  background-color: var(--subtlecolor);
  
}

button.button-play {
  margin: auto;
  font-size: 1.5rem;
  border-radius: 50%;
  background-color: var(--accentcolor);
  width: 5rem;
  height: 5rem;
  border: 1px solid #666;
  margin-bottom: 2rem;
  text-align: center;
}
button.button-play:hover {
  background-color: var(--accentcolorhighlight);
}


button.button-theme-toggle {
  font-size: 1rem;
  width: auto;
  height: auto;
  background-color: transparent;
  border: 0;
  border-radius: 0;
  display: block;
  text-align: right;
  margin: 0;
}








fieldset {
  border: 0px;
  padding: 0;
  margin: 0;
}

.spinner-inputs {
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-self: center;
  color: white;

}

.spinner {
  display: flex;
  align-items: center;
  margin-top: .5rem;
}

